.section-hero {
  position: relative;
  padding: 230px 0 0 0;
  background-image: linear-gradient(
      90deg,
      rgba(111, 144, 154, 0.6) 0%,
      rgba(111, 144, 154, 0.6) 50%,
      rgba(111, 144, 154, 0.6) 100%
    ),
    url(../images/hero/bg/hero-bg-1.jpg);
  background-repeat: no-repeat;
  background-size: cover, cover;
  background-position: center, center;

  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        90deg,
        rgba(111, 144, 154, 0.6) 0%,
        rgba(111, 144, 154, 0.6) 50%,
        rgba(111, 144, 154, 0.6) 100%
      ),
      url(../images/hero/bg/hero-bg-1@2x.jpg);
  }

  @include for-size(tablet) {
    padding: 300px 0 0 0;
  }
  @include for-size(desktop) {
    padding: 400px 0 0 0;
  }

  .title {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 170px;
    font-size: 30px;
    line-height: 1.2;
    text-align: right;
    color: var(--white-cl);
    z-index: 2;

    &::before {
      content: "";
      position: absolute;
      top: -80%;
      right: 20%;
      display: block;
      width: 230px;
      height: 160px;
      background-image: linear-gradient(
          90deg,
          rgba(0, 65, 66, 0.4009978991596639) 0%,
          rgba(0, 65, 66, 0.4009978991596639) 50%,
          rgba(0, 65, 66, 0.39539565826330536) 100%
        ),
        url(../images/hero/bg-img-4.jpg);
      background-repeat: no-repeat;
      background-size: cover;
      box-shadow: 3px 8px 24px 0px rgba(66, 68, 90, 1);
      z-index: -1;

      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: linear-gradient(
            90deg,
            rgba(0, 65, 66, 0.4009978991596639) 0%,
            rgba(0, 65, 66, 0.4009978991596639) 50%,
            rgba(0, 65, 66, 0.39539565826330536) 100%
          ),
          url(../images/hero/bg-img-4@2x.jpg);
      }

      @media screen and (min-width: 375px) {
        width: 275px;
        height: 190px;
      }
      @media screen and (min-width: 425px) {
        width: 300px;
        height: 220px;
      }
      @include for-size(mobile) {
        width: 365px;
        height: 270px;
      }
      @include for-size(tablet) {
        width: 70%;
        height: 360px;
      }
      @include for-size(desktop) {
        height: 500px;
      }
    }

    @media screen and (min-width: 375px) {
      margin-bottom: 200px;
    }
    @media screen and (min-width: 400px) {
      margin-bottom: 230px;
      font-size: 38px;
    }
    @include for-size(mobile) {
      font-size: 46px;
    }
    @include for-size(tablet) {
      margin-bottom: 400px;
      font-size: 56px;
    }
    @include for-size(desktop) {
      font-size: 76px;
    }

    .text-invert {
      display: inline-block;
      padding: 2px;
      color: var(--btn-cl);
      background-color: var(--white-cl);
    }
  }

  .design-bottom {
    position: relative;
    padding: 15px 10px;
    width: 100%;
    background-color: var(--white-cl);

    @include for-size(tablet) {
      padding: 15px 10px 130px 25px;
    }
    @include for-size(desktop) {
      padding: 20px 25px;
    }
  }

  .list-social {
    @include for-size(desktop) {
      display: flex;
    }

    .item {
      &:not(:last-child) {
        margin-bottom: 15px;

        @include for-size(desktop) {
          margin: 0 35px 0 0;
        }
      }
    }

    .link {
      display: flex;
      align-items: center;

      .text {
        display: none;
        font-weight: 600;
        color: var(--main-text-cl);

        @include for-size(tablet) {
          display: block;
        }
        @include for-size(desktop) {
          font-size: 18px;
        }
      }
    }
    .icon {
      margin-right: 10px;
      width: 28px;
      height: 28px;
      fill: var(--btn-cl);
    }
  }
}
