body {
    font-size: 14px;
    font-weight: 400;
    line-height: 1.8;
    font-style: normal;
    color: var(--main-text-cl);
}
img {
    display: block;
    max-width: 100%;
    height: auto;
}
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}
h1,
h2,
h3,
p {
    margin: 0;
}
.link {
    display: inline-block;
    text-decoration: none;
}
.container {
    width: 100%;
    padding: 0 15px;

    @include for-size(mobile) {
        width: 480px;
        margin: 0 auto;
    }
    @include for-size(tablet) {
        width: 768px;
    }
    @include for-size(desktop) {
        width: 1200px;
    }
}